<template>
    <div id="app">
        <el-card class="box-card" id="volTitle">
            <div style="font-size: 30px">在留言板上写出自己想说的话</div>
        </el-card>
        <div id="comments">
            <el-input
                    id="commentsInput"
                    :rows="20"
                    placeholder="请输入回复内容,字数限制200字"
                    minlength="1"
                    maxlength="200"
                    style="top: 8px"
                    type="textarea"
                    v-model="myComments">
            </el-input>
            <el-button @click="commentsSubmit()" icon="el-icon-check"
                       style="left: 47%;position: relative;margin-top: 10px;top: -30px"
                       type="primary"></el-button>
        </div>
        <span>将自己的想说的话写到留言板上,欢迎和大家一起讨论</span>
    </div>
</template>

<script>
    export default {
        name: "PutMessage",
        data() {
            return {
                myComments: '',
            }
        },
        methods: {
            commentsSubmit() {
                let that = this;
                let formData = new FormData();
                formData.append('myComments', this.myComments);
                let endDate = this.getNowFormatDate();
                formData.append('date', endDate);
                this.axios({
                    method: 'post',
                    url: '/writeMessage',
                    data: formData,
                })
                    .then(function (response) {
                            if (response.data.msg === '发表成功') {
                                that.$message.success(response.data.msg);
                                that.myComments = '';
                            } else {
                                that.$message.error(response.data.msg)
                                that.myComments = '';
                            }
                        },
                        function (err) {
                            console.log(err);
                        }
                    );
            },

            getNowFormatDate() {
                let date = new Date();
                let seperator1 = "-";
                let year = date.getFullYear();
                let month = date.getMonth() + 1;
                let strDate = date.getDate();
                if (month >= 1 && month <= 9) {
                    month = "0" + month;
                }
                if (strDate >= 0 && strDate <= 9) {
                    strDate = "0" + strDate;
                }
                let currentDate = year + seperator1 + month + seperator1 + strDate;
                return currentDate;
            }
        }
    }
</script>

